import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DeliverAwardManageComponent } from './deliverAwardManage/deliverAwardManage.component';
import { LotteryManageComponent } from './lotteryManage/lotteryManage.component';

const routes: Routes = [
  {
    path: 'deliverAwardManage/index',
    component: DeliverAwardManageComponent
  },

  {
    path: 'lotteryManage/index',
    component: LotteryManageComponent
  } 
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ActivityRoutingModules {}
